.c-network-nav {
	list-style-type: none;
	display: -ms-flexbox;
	display: flex;
	padding: 0;
	margin: 0;
	-ms-flex: 1 1;
	    flex: 1 1;
}

.c-network-nav__link {
	-ms-flex: 1 1;
	    flex: 1 1;
	text-align: center;
}

.c-network-nav__link--non-active-link {
	border-bottom: 2px solid #808080;
}

.c-network-nav .c-network-nav__link--active-link {
	color: #FD786D;
	border-bottom: 2px solid #FD786D;
}

.c-network-nav a {
	padding: 1em 0 1em 0;
	text-decoration: none;
	width: 100%;
	display: inline-block;
	font-family: Arial;
	text-transform: uppercase;
	color: #808080;
	font-size: 2.5vh;
}


@media(min-width: 769px) {
	.c-network-nav .c-network-nav__link--active-link, .c-network-nav .c-network-nav__link--non-active-link {
		padding-bottom: 10px;
	}
}



