/**  Layout  **/

/**  Components  **/
.c-network-request-thumbnail {
    display: -ms-flexbox;
    display: flex;
    border-top: 1px solid #9C9B9B;
    -ms-flex-align: center;
        align-items: center;
	padding: 1em 0 1em 0;
    background-color: white;
    /* background-color: rgba(115, 201, 177, .8); */
    
}

.c-network-request-thumbnail__user-img {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-left: .6em;
    -ms-flex-preferred-size: 18%;
        flex-basis: 18%;
    max-width: 200px;
}

.c-network-request-thumbnail__user-img img {
    width: 75%;
    border-radius: 50%;
}

.c-network-request-thumbnail__link {
    color: black;
}

.c-network-request-thumbnail__link:hover, .c-network-request-thumbnail__link:active {
    color: black;
    text-decoration: none;
}

.c-network-request-thumbnail__details {
    width: 80%;
}

.c-network-request-thumbnail__details__full-name {
    font-family: Arial;
    margin: 0;
    padding-left: 1em;
    font-weight: 500;
    font-size: 2.5vh;
    max-width: 50%;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.c-network-request-thumbnail-link-wrapper {
        text-decoration: none;
        color: black;
}

.c-network-request-thumbnail__button-wrapper {
	position: absolute;
    right: 18px;
}

.c-network-request-thumbnail__button-wrapper .btn-default {
    background-color: rgba(115, 201, 177, .9);
    color: white;
}

@media(min-width: 769px) {
    .c-network-request-thumbnail__button-wrapper {
        position: static;
        margin-right: 18px;
    }
    .c-network-request-thumbnail__details__full-name {
        max-width: 90%;
    }
}


