.c-contact-list-header {
    text-align: center;
    margin: 0;
    border-bottom: 1px solid #808080;
    padding: .7em 0;
    font-size: 4vh;
    font-weight: 400;
}

@media(min-width: 769px) {
    .c-contact-list {
        -ms-flex-preferred-size: 30%;
            flex-basis: 30%;
        height: 73.5vh;
        overflow: scroll;
        background-color: white;
        border: 1px solid #9C9B9B;
    }
}



