.l-network-content .container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	/* margin: auto; */
}

.l-network-content input {
	-ms-flex: 1 1;
	    flex: 1 1;
}

.l-network-content {
	padding-top: 8vh;
	-ms-flex-item-align: start;
	    align-self: flex-start;
}

.l-network-content .c-network-button {
	-ms-flex-preferred-size: 40px;
	    flex-basis: 40px;
}
