.plus {
	margin: 0 .7em;
}

@media(max-width: 768px) {
    .l-network-content .container {
		margin: 0;
		padding: 0;
		height: 100vh;
	}
	.l-network-content {
		-ms-flex: 1 1;
		    flex: 1 1;
	}
}

@media(min-width: 769px) {
    .l-network-content .container {
		margin: default;
		padding: default;
		height: 100%;
	}
	.l-network-content {
		-ms-flex: 1 1;
		    flex: 1 1;
		height: 100vh;
	}
	.l-network-content > div {
		height: 100%;
	}
	.topNav {
		-ms-flex-item-align: start;
		    align-self: flex-start;
		width: 30%;
	}
	
	.messageConversation, .desktopContacts, .connectContacts, .contactsNewMessage {
		display: -ms-flexbox;
		display: flex;
		width: 100%;
		margin: auto;
	}
}



